.oval-container-form {
    background: #FFFFFF;
    border-radius: 30px;
    padding: 10px;
    .flex-card {
        display: flex;
        justify-content: space-between;
        &:not(:last-child) {
            margin-bottom: 10px;
        }

        & > div {
            width: 100%;
            .form-control-oval {
                width:calc( 100% - 10px);
            }
        }
    }
}
.clearfix {
    &:after {
        clear: both;
        content: "";
        display: table;
    }
}

* {
    box-sizing: border-box;
    font-family: $Circe;
}

a {
    text-decoration: none;
