}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

.btn-skew {
    position: relative;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    padding: 12px 30px;
    background: transparent;
    border: none;
    outline: none;
    color: #FFFFFF;
    perspective: 600px;
    z-index: 3;
    transform-style: flat;
    transform-origin: center right;

    span {
        position: relative;
    }

    &:before {
        width: calc(100% + 70px);
        height: 100%;
        content: "";
        left: -48px;
        top: 0;
        background: rgba(11, 45, 96, 0.3);
        border: 1px solid rgba(0, 0, 0, 0.1);
        box-sizing: border-box;
        border-radius: 120px 116px 116px 120px;
        transform: rotateY(-50deg);
        position: absolute;
    }
}

.list-ol {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;

    & > div {
        width: 323px;
        min-height: 272px;
        background: #FFFFFF;
        border-radius: 20px;
        padding: 25px;
        box-shadow: 20px 40px 120px #B6BCD6;
        border-bottom: 6px solid #D9D9F1;

        .icon-bar {
            width: 153px;
            height: 155px;
            border-radius: 107px;
            margin: -98px 0 0 -17px;
            position: relative;

            background-position: 0 0;
            background-repeat: no-repeat;
            background-size: cover;

            span {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }


            &.bg-1 {
                background-image: url('../images/icons/1.png');
            }

            &.bg-2 {
                background-image: url('../images/icons/2.png');
            }

            &.bg-3 {
                background-image: url('../images/icons/3.png');
            }

            &.bg-4 {
                background-image: url('../images/icons/4.png');
            }


            span {
                font-family: $Circe;
                font-style: normal;
                font-weight: 800;
                font-size: 40px;
                line-height: 59px;
                color: #FFFFFF;
                border: 1px solid #26BD44;
            }
        }

        .title {
            margin-bottom: 25px;

            .h6 {
                font-family: $Circe;
                font-style: normal;
                font-weight: bold;
                font-size: 22px;
                line-height: 1.27;
                color: #2D2C42;
            }
        }

        .description {

            font-family: $Circe;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            /* or 125% */


            color: #3E3C56;

        }

    }
}

