.input-mask-group {
    display: block;
    width: 200px;
    position: relative;

    transition: all 0.4s ease;

    &:before {
        content: "";
        position: absolute;
        top: -5px;
        left: -29px;
        width: calc(100% + 7px);
        height: calc(100% + 8px);
        border-radius: 180px;
        background: #D9E0ED;
        border: 2px solid #CFD4E2;
        transform: perspective(156px) rotateY(-16deg);
        transition: inherit;
    }

    &:hover::before {
      box-shadow: 0px 16.9767px 33.9535px #9192A1;
    }

    .btn-icon {
        width: 48px;
        height: 47px;
        background: linear-gradient(180deg, #A254C5 -9.79%, #5948C8 103.6%);
        border-radius: 180px;
        display: inline-block;
        vertical-align: middle;
        margin: -2px 0 0 12px;
        background-position: 0 0;
        position: relative;
        z-index: 4;
        box-shadow: 4px 4px 5px rgba(125, 85, 238, 0.49);

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    input {
        background: transparent;
        border: none;
        display: inline-block;
        width: calc(100% - 70px);
        vertical-align: middle;
        padding: 9px 0 5px 9px;
        outline: none;

        position: relative;
        z-index: 4;

        font-family: $Circe;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;

        color: #9192A1;
    }
}

