.btn1 {
    border: 0;
    outline: none;
    background-color: transparent;
    font-family: $Circe;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    cursor: pointer;
    position: relative;
    background-repeat: no-repeat;
    background-position: 0 0;
    text-align: center;
    display: block;
    background-size: 100% 100%;

    &.btn-inline {
        display: inline-block;
    }

    .btn1-sm {
        font-size: 12px !important;
        padding: 7px 22px !important;
    }

    &.btn1-md {
        font-size: 12px !important;
        padding: 8px 25px !important;
    }

    &.btn1-lg {
        font-size: 16px !important;
        padding: 18px 50px !important;
    }


    &.btn-orange {
        background-image: url("../images/btn-bg/img-orange.png");
        transition: all 0.3s ease;
        color: #FFFFFF;
        width: 180px;
        height: 67px;
        line-height: 67px;
        box-sizing: border-box;
    }

    &.btn-cyan {
        background-image: url("../images/btn-bg/Rectangle 5.png");
        transition: all 0.3s ease;
        color: #FFFFFF;
        padding: 20px 25px;

        &.btn-sm {
            font-size: 12px !important;
            padding: 7px 12px !important;
            box-shadow: 10px 20px 25px #EDF0FA;
        }

        &:before {
            content: "";
            position: absolute;
            bottom: 0;
        }

        &:hover {
            &:before {
                box-shadow: 0px 16.9767px 33.9535px rgba(90, 147, 255, 0.5);
            }
        }
    }

    &.btn-simple {
        background-image: url("../images/btn-bg/Rectangle 54.png");
        transition: all 0.3s ease;
        color: #ffffff;
        padding: 15px 20px;
    }

    &.btn-gray {
        background-image: url("../images/btn-bg/btn-33.png");
        transition: all 0.3s ease;
        font-size: 12px;
        line-height: 18px;
        color: #3E3C56;
        padding: 15px 12px;

    }

    &.btn-default {
        transition: all 0.3s ease;
        color: #9192A1;
        padding: 10px 10px;
        border-radius: 60% 40% 40% 60%/50%;
