.btn {
    border: 0;
    outline: none;
    background-color: transparent;
    font-family: $Circe;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    cursor: pointer;
    position: relative;
    background-repeat: no-repeat;
    background-position: 0 0;
    text-align: center;
    display: inline-block;
    background-size: 100% 100%;
    border-radius: 180px;
    perspective: 163px;
    transition: all 0.3s ease;

    &.text-underline {
        span {
            text-decoration: underline;
        }
    }

    span {
        position: relative;
        z-index: 7;
    }

    &:before {
        transition: all 0.4s ease;
        content: "";
        position: absolute;
        border-radius: 180px;
        transform: perspective(500px) rotateY(-12deg);
        z-index: 4;
        top: 0px;
        left: -16px;
        width: calc(100% + 1px);
        height: 100%;
    }

    &:after {
        transition: all 0.4s ease;
        content: "";
        position: absolute;
        border-radius: 180px;
        box-sizing: border-box;
        transform: perspective(500px) rotateY(-12deg);
        z-index: 2;
        top: -1px;
        left: -17px;
        width: calc(100% + 3px);
        height: calc(100% + 3px);
    }

    .btn-icon-white {
        vertical-align: middle;
        margin-right: 5px;
        padding: 0 8px 0 0;
    }

    &.btn-inline {
        display: inline-block;
    }

    &.btn-sm {
        font-size: 12px !important;
        // padding: 7px 24px !important;
        padding: 5px 22px !important;

        &:before {
            top: 0px;
            left: -6px;
            width: calc(100% + 1px);
            height: 100%;
        }

        &:after {
            top: -1px;
            left: -7px;
            width: calc(100% + 3px);
            height: calc(100% + 3px);
        }
    }

    &.btn-md {
        font-size: 16px !important;
        padding: 16px 20px !important;
        min-width: 143px;

        &:before {
            top: 0;
            left: -10%;
            //left: -16px;
            width: calc(100% + 1px);
            height: 100%;
        }

        &:after {
            top: -1px;
            //left: -17px;
            left: calc(-10% - 1px);
            width: calc(100% + 3px);
            height: calc(100% + 3px);
        }
    }

    &.btn-lg {
        font-size: 16px !important;
        padding: 18px 25px !important;
        min-width: 176px;

        &:before {
            top: 0;
            left: -16px;
            width: calc(100% + 1px);
            height: 100%;
        }

        &:after {
            top: -1px;
            left: -17px;
            width: calc(100% + 3px);
            height: calc(100% + 3px);
        }
    }

    .btn-icon {
        width: 48px;
        height: 47px;
        background: linear-gradient(180deg, #A254C5 -9.79%, #5948C8 103.6%);
        border-radius: 180px;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        margin: 0 0 0 15px;
        background-position: 0 0;

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &.btn-orange {
        color: #ffffff;

        &:before {
            background: linear-gradient(180deg, #FFCE56 0%, #FD945F 100%);
            border-bottom: 3px solid #dc6d27;
            border-right: 1px solid #de7534;
            border-left: 1px solid #fd9770;
            border-top: 1px solid #fd9770;
            top: 0;
            left: -10%;
            width: calc(100% + 1px);
            height: 100%;
        }

        &:after {
            background-image: linear-gradient(to bottom, #ffc956, #fd985e);
            top: 2px;
            left: calc(-10% - 0px);
            width: calc(100% + 3px);
            height: calc(100% + 0px);
        }

        &:hover {
            &:before {
                box-shadow: 0px 16.9767px 33.9535px rgba(253, 148, 95, 0.53);
            }
        }

        &.btn-sm {
            font-size: 12px !important;
            padding: 7px 12px !important;
        }
    }

    &.btn-simple {
        background-image: url("../images/btn-bg/Rectangle 54.png");
        transition: all 0.3s ease;
        color: #ffffff;
        padding: 15px 20px;
    }

    &.btn-gray {
        background-image: url("../images/btn-bg/btn-33.png");
        transition: all 0.3s ease;
        font-size: 12px;
        line-height: 18px;
        color: #3E3C56;

        &.btn-gray-sm {
            padding: 12px 22px;
        }
    }

    &.btn-default {

        transition: all 0.3s ease;
        color: #9192A1;
        padding: 10px 10px;
        border-radius: 180px;
        perspective: 163px;

        span {
            position: relative;
            z-index: 7;
        }

        &:before {
            content: "";
            position: absolute;
            top: -3px;
            left: -10%;
            width: calc(100% + 3px);
            height: 100%;
            border-radius: 180px;
            background: #D9E0ED;
            border: 2px solid #CFD4E2;
            transform: perspective(500px) rotateY(-12deg);
        }

        &.btn-sm {
            font-size: 12px !important;
            padding: 7px 12px !important;
        }

        &:hover::before {
            box-shadow: 0px 16.9767px 33.9535px #9192A1;
        }
    }

    &.btn-cyan {
        color: #ffffff;

        &:before {
            background: linear-gradient(180deg, #86E7FD 0%, #6498FC 100%);
        }

        &:after {
            background-image: linear-gradient(to bottom, #7dcaff, #5c8aec);
            border-bottom: 4px solid #5c8aec;
            border-top: 2px solid #7dcaff;
        }

        &:hover {
            &:before {
                box-shadow: 0px 16.9767px 33.9535px rgba(90, 147, 255, 0.5);
            }
        }
    }
}

